<template>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h4 name="header" class="modal-title">
                            Old Version
                        </h4>
                        <button @click="ClosePopupOldVersion" class="close"><em aria-hidden="true"
                                class="fa fa-times"></em> </button>
                    </div>
                    <div class="listing py-3 px-3">
                        <div class="table-responsive table-fix-header" id="div_forListView">
                            <table class="table table-bordered mt-0" style="width: 100%;" id="tblFileList">
                                <thead class="thead-bg">
                                    <tr>
                                        <th style="width:50px !important;" class="text-center"><input id="chkAll"
                                                class="chkitems" value="" type="checkbox"></th>
                                        <th>
                                            <span>FileName</span>
                                        </th>
                                        <th>
                                            <span>Version</span>
                                        </th>
                                        <th>
                                            <span>Uploaded By</span>
                                        </th>
                                        <th data-width="5%" class="text-center nonresize">
                                            <span>Action</span>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="item in filesOldVersionListing" :key="item">
                                        <td class="text-center"><input class="chkItems" type="checkbox">

                                        </td>

                                        <td>
                                            <span class="text-center">
                                                {{ item.FILE_NAME }}
                                            </span>
                                        </td>
                                        <td>
                                            <span class="text-center">
                                                {{ item.FILE_VERSION }}
                                            </span>
                                        </td>
                                        <td>
                                            <span class="text-center">
                                                {{ item.POSTED_BY }}
                                            </span>
                                        </td>
                                        <td class="text-center single-action">
                                            <em class="fa fa-download text-info action-icon" aria-hidden="true" @click="DownloadFiles(item.FILE_VERSION_ID,item.FILE_NAME)"></em>  
                                        </td>
                                    </tr>
                                    <tr>
                                        <td v-if="filesOldVersionListing.length == 0" colspan="7"
                                            class="text-danger text-center no-record">No
                                            Record found</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>
<script>
import ListMenu  from "../../../components/LeftMenu.vue"
export default {
    components: {
        ListMenu
    },
    props: {
        filesOldVersionListing: {
            type: Array,
            required: true
        }
    },
    data(){
        return{
            fileId: '', 
            fileName: '',
            
        }
    },
    methods: {
        ClosePopupOldVersion: function () {
            var vm = this;
            vm.$emit("closeOldVersionPopUp")
        },
        DownloadFiles: function(fileId, fileName)
        {
            
            var vm=this;
            var IsVersion=1;
            vm.DownloadFile(fileId, fileName,IsVersion)
        },

    }
}
</script>